<template>
	<view class="content">
		<view class="item" v-for="(n, index) in user_data" :key="index" @click="go_userinfo(n.id)">
			<image src="../../static/img1.jpg" mode="aspectFill"></image>
			<view class="user_info">
				<view class="_top">
					<text>昵称：{{n.name}}</text>
					<text>性别：{{n.gender}}</text>
					<text>年龄：{{n.age}}</text>
				</view>
				<view class="_middle">
					<text>工作单位：{{n.unit}}</text>
					<text>职业：{{n.professional}}</text>
				</view>
				<view class="_bottom">
					<text>所在区域：{{n.city}}{{n.area}}</text>
				</view>
			</view>
		</view>
		<uni-load-more :status="loadingType"></uni-load-more>
	</view>
</template>

<script>
	import uniLoadMore from '../components/uni-load-more/uni-load-more.vue'
	import _ from 'lodash'
	export default {
		data() {
			return {
				user_data: [],
				pageNum: 1,
				loadingType: 'more',
				more_page: 0
			}
		},
		components:{
			uniLoadMore
		},
		onReachBottom(){
			if (this.pageNum <= this.more_page) {
				uni.showLoading({
				    title: '加载中'
				});
				this.init()
			}
		},
		onLoad() {
			uni.showLoading({
			    title: '加载中'
			});
			this.init()
			uni.setStorageSync('userId', '31e372d6-f972-11ea-8349-309c238dbd6f')
		},
		methods: {
			go_userinfo (n) {
				console.log(n)
				uni.navigateTo({
					url: `/pages/user/userinfo?id=${n}`
				})
			},
			init () {
				this.$http
				.post('/api/userlist', 
				{
					id : uni.getStorageSync('userId'),
					pageNum: this.pageNum,
					pageSize: 5
				}, {})
				.then(res => {
					console.log(res)
					_.each(res.data.rows, o => {
						this.user_data.push(o)
					})
					uni.hideLoading()
					this.more_page = Math.ceil(res.data.total/5)
					if (this.pageNum === this.more_page) {
						this.loadingType = 'nomore'
					}
					this.pageNum++
				})
				.catch(err => {
				})
			}
		}
	}
</script>

<style lang="scss">
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		.item{
			border-radius: 20upx;
			position: relative;
			width: 98%;
			height: 440upx;
			overflow: hidden;
			margin-top: 10upx;
			image{
				width: 100%;
				height: 440upx;
			}
			.user_info{
				position: absolute;
				bottom: 0;
				width: 100%;
				height: 160upx;
				background-color: rgba(255,255,255,.5);
				display: flex;
				flex-direction: column;
				justify-content: center;
				box-sizing: border-box;
				padding: 10upx 20upx;
				text{
					color: #000;
					font-size: 28upx;
				}
				view{
					display: flex;
					flex: 1;
					align-items: center;
				}
				._top{
					text{
						width: 130upx;
					}
					text:nth-child(1){
						width: 280upx;
					}
				}
				._middle{
					text:nth-child(1){
						width: 400upx;
					}
				}
			}
		}
	}
</style>
